.pokemon-card {
    padding: 10px 20px 20px;
    text-align: center;
    background: #fff;
    border-radius: 20px;
    box-shadow: 10px 10px 60px #e9e8e7;
    flex: 0 0 300px;
    margin-bottom: 30px;
    cursor: pointer;
    transition: all .2s ease-in-out;
}
.pokemon-card:hover { transform: scale(1.1); }
.pokemon-card-image {
    text-align: center;
    position: absolute; 
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
}
.pokemon-card-image-list {
    text-align: center;
    position: absolute; 
    left: 50%;
    top: 29%;
    transform: translate(-50%, -50%);
}
.pokemon-card-owner-image {
    text-align: center;
    position: absolute; 
    left: 50%;
    top: 27%;
    transform: translate(-50%, -50%);
}
.pokemon-card-image, .pokemon-card-owner-image, .pokemon-card-image-list {
    align-items: center;
    display: flex;
    -webkit-filter: drop-shadow(4px 4px 4px rgba(4,8,12,.2));
    filter: drop-shadow(4px 4px 4px rgba(4,8,12,.2));
    justify-content: center;
    min-width: 96px;
    min-height: 96px;
    z-index: 10;
}

/* NUMBER */
.pokemon-card-number {
    color: #e9e8e7;
    font-size: 52px;
    font-weight: 500;
    position: relative;
    top: -20px;
}

/* NAME */
.pokemon-card-name {
    font-size: 20px;
    font-weight: 500;
    margin-top: 18px;
}

/* Mobile Devices */
@media (min-width: 320px) and (max-width: 480px) {
    .pokemon-card-name {
        font-size: 16px;
    }
}
  
/* Small Screens, Laptops */
@media (min-width: 769px) and (max-width: 991px) {
    .pokemon-card-name {
        font-size: 19px;
    }
